<template>
  <v-app id="JMFC">
    <cv-navigation
      v-model=drawer
      :contact=contact
      :items=navigationItems
      :picture=picture
      @select=select
    />
    <v-app-bar
      :clipped-left=mobile
      app
      color="primary"
      :dark="true"
    >
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title
        class="ml-0 pl-4"
      >
        {{ name }}
      </v-toolbar-title>

      <v-spacer />

      <v-tooltip left>
        <template v-slot:activator="{ on }">
          <v-btn
            @click="dark = !dark"
            v-on=on
            icon
          >
            <v-icon v-text="'$dark'" />
          </v-btn>
        </template>
        <span>
          {{ this.t('darkMode') }}
          {{ this.t(dark ? 'light' : 'dark') }}
        </span>
      </v-tooltip>
      <v-tooltip left>
        <template v-slot:activator="{ on }">
        <v-btn
          @click=switchLanguage()
          v-on=on
          icon
        >
          <v-icon v-text="'$lang'" />
        </v-btn>
        </template>
        <span>{{ this.t('switchLanguage') }}</span>
      </v-tooltip>
      {{ language }}
    </v-app-bar>

    <v-main>
      <v-container
        class="fill-height px-0"
        fluid
      >
        <v-row
          align=center
          justify=center
          no-gutters
        >
        <cv-contact v-if="tab == 'contact'" :contact=contact :picture=picture />
        <cv-education v-if="tab == 'education'" :data=education />
        <cv-jobs v-if="tab == 'work'" :data=work />
        <cv-skills v-if="tab == 'skills'" :data=skills />
        </v-row>
      </v-container>
    </v-main>
    <v-fade-transition>
      <v-btn
        :aria-label="t('top')"
        v-scroll="onScroll"
        v-show="showButton"
        fab
        dark
        fixed
        bottom
        right
        color=primary
        transition=slide-y-transition
        @click=toTop
      >
      <v-icon
        :aria-hidden="!showButton"
        v-text="'$top'"
      />
      </v-btn>
    </v-fade-transition>
  </v-app>
</template>

<script>
const cvContact = () => import('@/components/cvContact.vue');
const cvEducation = () => import('@/components/cvEducation.vue');
const cvJobs = () => import('@/components/cvJobs.vue');
const cvNavigation = () => import('@/components/cvNavigation.vue');
const cvSkills = () => import('@/components/cvSkills.vue');

export default {
  props: {
    source: String,
  },
  components: {
    cvContact,
    cvEducation,
    cvJobs,
    cvNavigation,
    cvSkills,
  },
  data: () => ({
    dialog: false,
    drawer: null,
    navigationItems: null,
    picture: 'https://media-exp1.licdn.com/dms/image/C4E03AQFmke_5gxkYDw/profile-displayphoto-shrink_100_100/0?e=1601510400&v=beta&t=5QCDN6qsQ_Fbg0aayxTBsEfLU4kbm7At_FG8zW34gnc',
    showButton: false,
    tab: 'contact',
  }),
  methods: {
    onScroll() {
      this.showButton = window.scrollY > window.innerHeight / 4;
    },
    toTop() {
      this.$vuetify.goTo(0, { duration: 300 });
    },
    select(tab) {
      this.tab = tab;
      if (this.mobile) {
        this.drawer = false;
      }
    },
    setNavigationItems() {
      const socialNetworks = Object.keys(
        this.$vuetify.lang.locales[this.language].cv.social,
      );
      this.navigationItems = [
        {
          icon: '$contact',
          name: 'contact',
          text: this.t('bio'),
        },
        {
          icon: '$education',
          name: 'education',
          text: this.t('education'),
        },
        {
          icon: '$work',
          name: 'work',
          text: this.t('professional background'),
        },
        {
          icon: '$skills',
          name: 'skills',
          text: this.t('technologies'),
        },
        {
          icon: '$up',
          iconAlt: '$down',
          text: this.t('social'),
          model: false,
          children: Array.from(socialNetworks, (social) => (
            {
              icon: `mdi-${social}`,
              url: this.t(`cv.social.${social}`),
              name: social,
              text: social,
            }
          )),
        },
        {
          icon: '$less',
          iconAlt: '$more',
          text: this.t('more'),
          model: false,
          children: [
            {
              icon: '$cv',
              name: 'simplified',
              text: this.t('simplified version'),
              url: this.t('cv.misc.simplecv'),
            },
            {
              icon: '$blog',
              name: 'blog',
              text: this.t('blog'),
              url: this.t('cv.misc.blog'),
            },
          ],
        },
      ];
    },
    switchLanguage() {
      const locale = Object.keys(this.$vuetify.lang.locales)
        .filter((lang) => lang !== this.$vuetify.lang.current).pop();
      this.language = locale;
    },
  },
  created() {
    this.drawer = !this.mobile;
    this.setNavigationItems();
  },
  computed: {
    dark: {
      get() {
        return this.$vuetify.theme.isDark;
      },
      set(value) {
        this.$vuetify.theme.dark = value;
      },
    },
    contact() {
      return {
        ...this.tr('cv.contact'),
        ...this.tr('cv.identification'),
      };
    },
    education() {
      return this.tr('cv.education');
    },
    language: {
      get() {
        return this.$vuetify.lang.current;
      },
      set(locale) {
        this.$vuetify.lang.current = locale;
      },
    },
    large() {
      return this.$vuetify.breakpoint.lgAndUp;
    },
    name() {
      if (this.large) {
        return this.contact.fullName;
      }
      return this.mobile
        ? this.contact.shortName
        : `${this.contact.name} ${this.contact.surname}`;
    },
    skills() {
      return this.tr('skills');
    },
    work() {
      return this.tr('cv.background');
    },
  },
  watch: {
    language() {
      this.setNavigationItems();
    },
  },
};
</script>

<style lang="scss">
.theme--dark{
  .v-card, .v-timeline .v-timeline-item__dot {
    background: var(--v-accent-base) !important;
  }
  .v-data-table {
    background-color: #121212 !important;
  }
}
</style>
