import Vue from 'vue';
import App from './App.vue';
import vuetify from './plugins/vuetify';

Vue.config.productionTip = false;

Vue.mixin({
  computed: {
    mobile: () => vuetify.framework.breakpoint.smAndDown,
  },
  methods: {
    t: (text) => {
      let translated;
      try {
        translated = vuetify.framework.lang.translator(text);
      } catch (e) {
        translated = text;
      }
      return translated;
    },
    // get translation key (e.g. get an array/object from the locale file)
    tr: (key) => {
      const locale = vuetify.framework.lang.current;
      return key.split('.').reduce(
        (p, c) => (p && p[c]) || null,
        vuetify.framework.lang.locales[locale],
      );
    },
  },
});

new Vue({
  vuetify,
  render: (h) => h(App),
}).$mount('#app');
